*{
    margin: 0;
    padding: 0;
}
body{
    background:	#F4F4F4
}
.logo{
    display: flex;
    align-items: center;
}
.logo h1 {
    font-size: 20px;
    font-weight:bolder;
    color: #FFFFFF; /* Spotify-like green */
    letter-spacing: 2px;
    font-family:'Courier New', Courier, monospace;
}

.logo img {
    width: 50px; /* Adjust as needed */
    height: auto;
    vertical-align: middle;
    padding-right: .4rem;
}

.card:hover {
    transition: all .5s ease-in-out;
    transform: scale(1.05); /* Slight zoom effect */
    box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.3); /* Adds depth */
}

.middle, .end{
    display: flex;
    gap: 2rem;
    justify-content: space-evenly;
    font-size: 12px;
    font-weight:700;
    color: #FFFFFF; /* Spotify-like green */
    letter-spacing: 2px;
    font-family:'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
}
a:hover{
    transition: all .3s ease-in-out;
    color: 	#1ABC9C;

}
.search-container {
    display: flex;
    align-items: center;
    width: 500px;
    border: 2px solid #333;
    border-radius: 10px;
    overflow: hidden;
    background: white;
}

#search-input {
    flex: 1;
    padding: 10px;
    border: none;
    outline: none;
    font-size: 16px;
}

#search-button {
    padding: 10px 15px;
    border: none;
    background: #333;
    color: white;
    cursor: pointer;
    font-size: 16px;
}

#search-button:hover {
    background: #555;
}
.footer {
    background-color: #222;
    color: white;
    padding: 20px 0;
    text-align: center;
}

.footer-container {
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
    max-width: 1000px;
    margin: auto;
}

.footer-section {
    flex: 1;
    min-width: 250px;
    margin: 10px;
}

.footer-section h3 {
    font-size: 18px;
    margin-bottom: 10px;
}

.footer-section p, .footer-section ul {
    font-size: 14px;
}

.footer-section ul {
    list-style: none;
    padding: 0;
}

.footer-section ul li {
    margin: 5px 0;
}

.footer-section ul li a {
    text-decoration: none;
    color: #ddd;
    transition: color 0.3s;
}

.footer-section ul li a:hover {
    color: #4CAF50;
}

.social-icons a {
    text-decoration: none;
    font-size: 20px;
    margin: 0 10px;
    color: white;
    transition: color 0.3s;
}

.social-icons a:hover {
    color: #4CAF50;
}

.footer-bottom {
    margin-top: 20px;
    font-size: 14px;
    border-top: 1px solid #444;
    padding-top: 10px;
}

